<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <p>Your IP is {{ myIP }}</p>
	<input type="text" v-model="reqURL" placeholder=v-model.reqURL />
        <button v-on:click="reqData()">Request</button>
        <br />
        <br />
    	<ol>
        	<li v-for="data in dataList"> {{ data.title }} </li>
    	</ol>
        <textarea>{{ response }}</textarea>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'HelloWorld',
  data () {
    return {
      reqURL: 'http://35.200.40.38:3000/api/scripts',
      myIP: '',
      msg: 'Welcome!!',
      input: '',
      response: '',
      dataList: ''
    }
  },
  mounted () {
    axios({ method: 'GET', 'url': 'https://httpbin.org/ip' }).then(result => {
      this.myIP = result.data.origin
    }, error => { console.error(error) })
  },
  methods: {
    reqData() {
      axios({ method: 'GET', 'url': this.reqURL,
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Content-Type': 'application/json'
        }
      }).then(result => {
        console.log( "OK" + JSON.stringify(result));
        console.log( "response = " + result.data.response );
        this.dataList = result.data.data;
        this.msg = result.data.msg;
        this.response = JSON.stringify(result);
      }, error => { 
        this.msg = "error : " + error;
        console.error(error) 
      })
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



